.ml-auto {
  margin-left: auto;
}

.navbar {
  border-bottom: 0.5px solid rgb(235, 226, 226); 
  box-shadow: rgb(149 157 165 / 20%) 0px 8px 24px; 
  position: fixed; 
  display: block; 
  width: 100%; 
  top: 0; 
  z-index: 10; 
  background: #fff;
}
.child_container {margin: 100px auto 50px;}

.btn-outline-danger:active:focus,
.btn-outline-danger:focus {
  box-shadow: none;
}

.btn-outline-danger:active,
.btn-outline-danger:hover {
  background-color: unset;
  color: #dc3545;
  border-color: #dc3545;
}

.dropdown-toggle-text {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-right: 5px;
}

.dropdown .dropdown-toggle {
  display: flex;
  align-items: center;
}

.f-14 {
  font-size: 14px;
}

.f-12 {
  font-size: 12px;
}

.navbar .nav-item {
  margin-right: 15px;
}

.navbar-light .navbar-nav .nav-link {
  color: #111;
}

.dropdown-menu {
  margin-top: 10px;
}

.logout-dropdown .dropdown-toggle::after {
  margin-top: 20px;
}

.footer-section {
  color: rgb(88, 85, 85);
  font-size: 14px;
  background: rgb(248, 246, 246);
  padding: 6px 0px;
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid #e2e2e2;
}

.dropdown-item.active, .dropdown-item:active { text-decoration: none; background-color: #efefef; color: #000; }